import React from "react";
import { CustomColumns, Environment } from "../../types";
import { useQuery } from "react-query3";
import { Button, PageHeader, Space } from "antd";
import { Modal } from "components/ui/Modal";

import Table from "components/ui/Table";
import EditModal from "components/ui/EditModal";
import DeleteButton from "components/ui/DeleteButton";
import EnvironmentModal from "./NewEnvironmentModal"
import { LinkOutlined } from "@ant-design/icons";

export default function Environments() {
  const { data: environments, isLoading, refetch } = useQuery<Environment[]>(
    "/environment",
    {
      enabled: false,
      refetchOnWindowFocus: false,
    }
  );

  const columns: CustomColumns<Environment>[] = [
    {
      key: "name",
      title: "Name",
      dataIndex: "name",
      width: "10%",
    },
    {
      key: "version",
      title: "Version",
      dataIndex: "version",
      width: "10%",
    },
    {
      key: "path",
      title: "Path",
      dataIndex: "path",
      width: "20%",
    },
    {
      key: "arguments",
      title: "Arguments",
      width: "25%",
      dataIndex: "arguments",
    },
    {
      key: "persistentRunspace",
      title: "Persistent Runspace",
      dataIndex: "persistentRunspace",
      render: (text, record) => record?.persistentRunspace ? "True" : "False"
    },
    {
      key: "highPerformanceRunspacePool",
      title: "High Performance Runspace Pool",
      dataIndex: "highPerformanceRunspacePool",
      render: (text, record) => record?.highPerformanceRunspacePool ? "True" : "False"
    },
    {
      key: "maxRunspaces",
      title: "Maximum Runspaces",
      dataIndex: "maxRunspaces"
    },
    {
      width: "fit-content",
      dataIndex: "actions",
      render: (text, record) => {
        return (
          <Space>
            <Modal>
              <EditModal record={record} allowedWithOneWayGitSync={false} />
            </Modal>
            <DeleteButton resource={record} allowedWithOneWayGitSync={false} />
          </Space>
        );
      },
    },
  ];

  return (
    <PageHeader title="Environments" subTitle="Execution environments for PowerShell scripts within Universal." extra={[
      <Button icon={<LinkOutlined />} href="https://docs.powershelluniversal.com/config/environments" target="_blank">Documentation</Button>
    ]}>
      <Table
        title="Environments"
        actions={[
          <Modal>
            <EnvironmentModal />
          </Modal>
        ]}
        data={environments}
        columns={columns}
        loading={isLoading}
        queryFn={refetch}
      />
    </PageHeader>
  );
}
