import React, { Fragment } from "react";
import { Button } from "antd";
import { Form, FormContentBase } from "components/ui/Form/Form";
import {
  ModalContent,
  ModalOpenButton,
  useModalContext,
} from "components/ui/Modal";
import { Environment, AccessControlTypes } from "types";

export default function EnvironmentModal() {
  const { onOk, form } = useModalContext();

  function handleOk() {
    onOk("/environment", "create", {} as Environment);
  }

  return (
    <Fragment>
      <ModalOpenButton requiredRoles={["Administrator"]} requiredAccessControls={AccessControlTypes.Create} allowedWithOneWayGitSync={false}>
        <Button children="Create New Environment" />
      </ModalOpenButton>
      <ModalContent title="Create new environment" onClickOk={handleOk} formName="create_environment_form">
        <Form schemaName="environment">
          <FormContentBase
            name="create_environment_form"
            form={form}
            preserve={false}
          />
        </Form>
      </ModalContent>
    </Fragment>
  );
}
